import { Component, OnInit, Input,Output,EventEmitter,OnChanges,SimpleChange } from '@angular/core';


declare function FullscreenToggle(Boolean) : void;
@Component({
  selector: 'app-status-bar',
  templateUrl: './status-bar.component.html',
  styleUrls: ['./status-bar.component.css']
})
export class StatusBarComponent implements OnInit,OnChanges {


  @Output() viewUpdated = new EventEmitter();
  @Input()
  current_view:number = 5;

  @Input()
  configuration;


  fullscreen:Boolean = false;
  autoplay_btn_text:String;
  autoplay:Boolean;

  constructor() {
    
   }


  ngOnInit() {
    
    this.autoplay = localStorage.getItem("autoplay")==="true";
  this.send_event_autoplay();
    this.setAutoplayBtn();
  //this.eventAutoplay.getEmitter().emit(this.autoplay);
     //this.viewUpdated.emit(this.current_view);
  }

  changeView = function (view){    
    this.current_view = view;
     this.viewUpdated.emit(this.current_view);
  }

  ngOnChanges(changes: {[propKey: string]: SimpleChange}) {
    for (let propName in changes) {
      if(propName=="configuration"){
        if(this.configuration)
          this.changeView(this.configuration[this.current_view]);
      }
    }
  }

  toggleFullscreen(){
    this.fullscreen=!this.fullscreen;
    FullscreenToggle(this.fullscreen);
  }

 @Output() autoplay_notification: EventEmitter<Boolean> = new EventEmitter();

  toggleAutoplay(){
    this.autoplay = !this.autoplay;
    this.setAutoplayBtn();

  localStorage.setItem('autoplay', this.autoplay+"");
  this.send_event_autoplay();
}
  send_event_autoplay(){
      var event = new CustomEvent('autoplay_changed',{
  detail: {
    autoplay:this.autoplay
  }
  });
  window.dispatchEvent(event);
}

setAutoplayBtn(){
  if(this.autoplay){
    this.autoplay_btn_text = "Autoplay On"
    }else{
    this.autoplay_btn_text = "Autoplay Off"
  }
}
}




