import { Component, OnInit,Input,OnChanges,SimpleChanges} from '@angular/core';
import { Inject, NgZone } from '@angular/core';

declare function update_main_slider(boolean,string) : void;
declare function update_global_slides(array,boolean) : void;
declare function update_main_slider_one() : void;
declare function update_slideshowTimer(int) : void;
declare function update_slideshowTimerVertical(int) : void;
declare function update_slideshowTimerOwl(int) : void;
declare function get_timers() : any;


declare function $(any) : any;

@Component({
  selector: 'app-multiple-slideshow',
  templateUrl: './multiple-slideshow.component.html',
  styleUrls: ['./multiple-slideshow.component.css']
})


export class MultipleSlideshowComponent implements OnInit,OnChanges {

  @Input() imageUpdate;

  constructor() {
      var timers = get_timers();
      this.slideshowTimer = timers['jssor_multiple_timer'];
      this.slideshowTimerVertical = timers['jssor_multiple_timer_vertical'];
      this.slideshowTimerOwl = timers['owl_duration'];
  }
  subslideshows = ['horiz_slide0','horiz_slide1'];
  current_location;
  update_oneslideshow:boolean = true;
  slideshowTimer = 5;
  slideshowTimerVertical = 5;
  slideshowTimerOwl = 5;

@Input() slideshow;
@Input() activeView;

inputChanged(event){
   update_slideshowTimer(this.slideshowTimer);
}
inputChangedVertical(event){
   update_slideshowTimerVertical(this.slideshowTimerVertical);
}
inputChangedOwl(event){
  if(this.slideshowTimerOwl<1) this.slideshowTimerOwl = 1;
   update_slideshowTimerOwl(this.slideshowTimerOwl);
}

  ngOnInit() {
        
    }

    ngOnChanges(changes: SimpleChanges) {
      var t = this;

      if(typeof changes["activeView"]!='undefined'){
        
          this.slideTo(this.activeView.slideshows[0]);
    setTimeout(function(){
        t.update_slideshow(true,'');
        },10);
        
            return;
            
      }

      if(typeof changes["slideshow"]!='undefined'){
        setTimeout(function(){
        try{
         this.update_global_slides(t.subslideshows,false);

        }catch(e){
          console.log(e);
        }
        },10)
       
      }
     
    // changes.prop contains the old and the new value...
  }

  transform(val) {
    return JSON.stringify(val, null, 2)
      .replace(' ', '&nbsp;')
      .replace('\n', '<br/>');
  }

  slideTo(i){

         // $("#owl_one_slide>.camera_slide").moveTo('#owl_one_slide');
    this.update_oneslideshow = false;
    this.current_location = i;
    var t = this;
    setTimeout(function(){
        t.update_slideshow(true,'');
        },10);
  }

checkLocation(){
  return typeof this.current_location!='undefined';
}
showByLocation(){
  return typeof this.activeView!='undefined'&&this.activeView.byLocation;
}

  update_slideshow(b,string){
    try{
      if(!this.showByLocation())
         update_main_slider(true,'');
         else{           

         update_main_slider_one();
         }

        }catch(e){
          console.log(e);
        }
  }
}



