import { Component, OnInit,Input,AfterViewInit } from '@angular/core';

import { Http,Response } from '@angular/http';
import 'rxjs/add/operator/map';
import * as _ from 'underscore';

    // import * as jquery from '../../assets/js/jquery-1.9.1.min.js';
    // import * as bootstrap from 'assets/js/bootstrap.min.js';
    // import * as moment from 'assets/js/moment.min.js';
    // import * as datetimepicker from 'assets/js/bootstrap-datetimepicker.min.js';

declare var moment: any;
declare var $: any;

@Component({
  selector: 'app-employee-stats',
  templateUrl: './employee-stats.component.html',
  styleUrls: ['./employee-stats.component.css']
})
export class EmployeeStatsComponent implements OnInit,AfterViewInit {

 @Input() stats;
 @Input() viewId;
groupByVal = "Name";
groups;
groupBy(newValue) {
    this.groupByVal = newValue;  
    this.update_data();
  
};
update_stats;
todayDay = 0;
year = -1;
week = -1;
month = -1;
years = [];
weeks = [];
months = [];
startWeek;
endWeek;


 loadAPI: Promise<any>;

  constructor(private http: Http) { }



public loadScript( url, callback ) {
  var script = document.createElement( "script" )
  script.type = "text/javascript";
    script.onload = function() {
      callback();
    };  

  script.src = url;
  document.getElementsByTagName( "head" )[0].appendChild( script );
}


  ngOnInit() {
var t = this;


var d = new Date();
this.todayDay = d.getDay();
console.log("todayDay",this.todayDay)
  //t.update_data();



  t.update_stats = setInterval(function () {

    t.update_data();
    }, 60 * 1000)//60 seconds



}


  ngAfterViewInit() {
    // setTimeout(function(){
    //   t.updateyearorWeek();
    // },500)
    this.updateyearorWeek();
  }

date_module = require('./date_module');


load_data(){
  var t= this;
new Promise((resolve) => {
            t.loadScript('/assets/js/jquery-1.9.1.min.js',()=>{resolve()});
  }).then(()=>{
    return new Promise((resolve) => {
              t.loadScript('/assets/js/bootstrap.min.js',()=>{resolve()});
    })
  }).then((resolve)=>{
    return new Promise((resolve) => {
              t.loadScript('/assets/js/moment.min.js',()=>{resolve()});
    })
  }).then((resolve)=>{
    return new Promise((resolve) => {
              t.loadScript('/assets/js/bootstrap-datetimepicker.min.js',()=>{resolve()});
    })
  }).then((resolve)=>{
    t.initialize_calendar.call(t);
    
  });
}
initialize_calendar(){
  if(typeof moment =='undefined'){
    return this.load_data();
  }
  var t = this;
   moment.locale('en', {
        week: { dow: 6 } // Monday is the first day of the week
        });

var today = new Date();
        $("#weeklyDatePicker").datetimepicker({
            format: 'DD.MM.YYYY',
              minDate:new Date().setFullYear(today.getFullYear()-1),
              maxDate:today,
              useCurrent: false
        });

        //Get the value of Start and End of Week
        $('#weeklyDatePicker').on('dp.change', function (e) {
            var value = $("#weeklyDatePicker").val();
            var firstDate = moment(value, "DD.MM.YYYY").day(0);
            var lastDate =  moment(value, "DD.MM.YYYY").day(6);
            t.startWeek = firstDate.toDate();
            t.endWeek = lastDate.toDate();
            $("#weeklyDatePicker").val(firstDate.format("DD.MM.YYYY") + " - " + lastDate.format("DD.MM.YYYY"));
            var d = firstDate.toDate();
            t.year = d.getFullYear();
             t.week = t.date_module.getWeek.call(d);
          t.update_data(t.year,t.week);
        });
}

updateyearorWeek(){
  this.initialize_calendar();
 this.update_data(this.year,this.week);
}


update_data(year = -1,week=-1){
var t = this;

  // console.log("Grouping by ",t.groupByVal);
  //         var gg = _.groupBy(t.stats.employees, function(d){return d[t.groupByVal]});
  //         t.groups = [];
  //         for (var prop in gg) {
  //                 // skip loop if the property is from prototype
  //                 if(!gg.hasOwnProperty(prop)) continue;
  //         t.groups.push({group:prop,elem:gg[prop]})

  //             }

  //         console.log(t.groups);
  t.http.get("/api/get_employees_stats/"+t.viewId+"?year="+t.year+"&week="+t.week)
        .map((res) => {
            t.stats.employees =  res.json();



        if(t.year<0)
            t.year = t.stats.employees.year;
            if(t.week<0)
            t.week = t.stats.employees.week;
            t.startWeek = t.stats.employees.startWeek;
            t.endWeek = t.stats.employees.endWeek;
            
            //console.log(t.stats.employees);

            //console.log("Grouping by ",t.groupByVal);
          var gg = _.groupBy(t.stats.employees, function(d){return d[t.groupByVal]});
          t.groups = [];
          for (var prop in gg) {
                  // skip loop if the property is from prototype
                  if(!gg.hasOwnProperty(prop)) continue;
          t.groups.push({group:prop,elem:gg[prop]})

              }

          console.log(t.groups);
          })
        .subscribe(
          data=>function(data){
          },
          err => console.log(err)
        );
 }
}



